<template>
  <ul class="list" @touchstart="touchredmore" @touchend="touchendsredmore">
    <div class="header">
      <div class="back">
        <i class="fa fa-angle-left" aria-hidden="true" @click="backup"></i>
      </div>
      <h1 v-if="$store.state.clickitem">
        {{ $store.state.clickitem.catename }}
      </h1>
    </div>

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
    >
      <van-cell>
        <li v-if="$store.state.clickitem">
          <div
            class="content"
            v-for="(item, index) in $store.state.daysitemslist"
            :key="index"
            @click="gocontenttody(item)"
          >
            <img :src="item.image" alt="" />
            <div class="cont">
              <h4>{{ item.cates[0].catename }}/{{ getmin(item.duration) }}</h4>
              <h3>{{ item.title }}</h3>
            </div>
          </div>
        </li>
        <ul class="newlist" v-if="newlist[0]">
          <li v-for="(items, index) in newlist" :key="index">
            <div
              class="content"
              v-for="(item, index) in items"
              :key="index"
              @click="gocontenttody(item)"
            >
              <img :src="item.image" alt="" />
              <div class="cont">
                <h4>
                  {{ item.cates[0].catename }}/{{ getmin(item.duration) }}
                </h4>
                <h3>{{ item.title }}</h3>
              </div>
            </div>
          </li>
        </ul>
      </van-cell>
    </van-list>
  </ul>
</template>

<script>
// import Item from "@/components/Item";
export default {
  name: "Item",
  data() {
    return {
      startPagx: null,
      error: false,
      iserror: false,
      finished: false,
      loading: false,
      current: 1,
      newlist: [],
      scrollTop: 0
    };
  },
  methods: {
    onScrollBottom() {
      this.$nextTick(() => {
        const container = this.$el;
        window.console.log(container);
        if (container)
          document.documentElement.scrollTop = container.scrollHeight;

        if (container.scrollHeight) {
          this.scrollTop = container.scrollHeight - 500;
        }
      });
    },
    touchredmore(event) {
      this.startPagx = event.touches[0].pageX;
      // window.console.log(this.startPagx);
    },
    touchendsredmore(event) {
      let x = event.changedTouches[0].pageX;
      if (x - this.startPagx >= 100) {
        this.$router.go(-1);
        // this.isactive = false;
      }
    },
    gocontenttody(item) {
      window.console.log(item.postid, item);
      this.$store.commit("submitIsactive", true);
      this.$store.commit("submitLikeItem", item);
      this.$router.push({
        path: "finditemcontent",
        query: { id: item.postid }
      });
    },
    backup() {
      // this.$store.commit("submiShowitem", false);
      this.$router.go(-1);
    },
    getmin(i) {
      let time = i;
      let n = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (n < 10) {
        n = "0" + n;
      }
      if (s < 10) {
        // window.console.log("0" + s);
        s = "0" + s;
      }
      let m = n + "'" + s + '"';
      return m;
    },
    //下拉拿数据
    getTab(i) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostByTab&p=" +
            i +
            "&size=10&tab=" +
            this.$route.query.id
        )
        .then(response => {
          // window.console.log(response.data.data);
          this.newlist.push(response.data.data);
          // this.daysitemslist = response.data.data;
          // this.$store.commit("submiTdaysitemslist", response.data.data);
        });
    },
    getCateId(i) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=" +
            i +
            "&size=10&cateid=" +
            this.$route.query.id
        )
        .then(response => {
          this.newlist.push(response.data.data);
        });
    },
    onLoad() {
      this.onScrollBottom();
      this.current += 1;
      if (Number(this.$route.query.id)) {
        this.getCateId(this.current);
      } else {
        this.getTab(this.current);
      }
      setTimeout(() => {
        // for (let i = 0; i < this.$store.state.lists.length; i++) {
        //   this.list.push(this.list.length + 1);
        // }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        // if (this.list.length <= this.$store.state.lists.length) {
        //   this.finished = true;
        // }
        // this.finished = false;
      }, 500);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      let c = vm.$el;
      // window.console.log(c, vm._data.scrollTop);
      if (vm._data.scrollTop) {
        c.scrollTop = vm._data.scrollTop;
      }
    });
  },
  created() {
    window.console.log("创建");
    // window.console.log(this.$route);
    // window.console.log(this.$route.query.id);
  }
};
</script>

<style lang="less">
ul.list {
  transition: all 0.5s;
  position: relative;
  top: 0vh;
  height: 100vh;
  overflow: hidden;
  // display: flex;
  overflow-y: scroll;
  width: 100%;
  height: 100vh;
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    .back {
      position: absolute;
      top: 0;
      left: 4%;
      color: white;
      font-size: 25px;
      line-height: 51px;
    }
    h1 {
      text-align: center;
      background: #161616;
      color: white;
      font-size: 1em;
      padding: 15px 0;
      letter-spacing: 2px;
    }
  }
  .van-cell {
    flex: 1;
    overflow: hidden;
    overflow-y: scroll;
    top: 51px;
    left: 0;
    flex: 1;
    padding: 0;
    li {
      .content {
        position: relative;
        height: 34vh;
        vertical-align: middle;
        img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
        }
        .cont {
          color: white;
          position: absolute;
          bottom: 15px;
          left: 15px;
          h4 {
            font-size: 10px;
            line-height: 28px;
            font-weight: 200;
            span {
              padding: 0px 5px;
            }
          }
          h3 {
            font-size: 14px;
          }
        }
      }
      .contents {
        color: red;
        display: flex;
        flex-wrap: wrap;
        .icon {
          width: 50%;
          height: 50%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
          .cont {
            color: white;
            position: absolute;
            bottom: 5px;
            left: 15px;
            h4 {
              font-size: 10px;
              line-height: 20px;
              font-weight: 200;
              span {
                padding: 0px 3px;
              }
            }
            h3 {
              font-size: 13px;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
          }
          &:nth-child(1) {
            display: none;
          }
        }
      }
    }
    ul.newlist {
      li {
        .content {
          position: relative;
          height: 34vh;
          vertical-align: middle;
          img {
            width: 100%;
            height: 100%;
            vertical-align: middle;
          }
          .cont {
            color: white;
            position: absolute;
            bottom: 15px;
            left: 15px;
            h4 {
              font-size: 10px;
              line-height: 28px;
              font-weight: 200;
              span {
                padding: 0px 5px;
              }
            }
            h3 {
              font-size: 14px;
            }
          }
        }
        .contents {
          color: red;
          display: flex;
          flex-wrap: wrap;
          .icon {
            width: 50%;
            height: 50%;
            position: relative;
            img {
              width: 100%;
              height: 100%;
            }
            .cont {
              color: white;
              position: absolute;
              bottom: 5px;
              left: 15px;
              h4 {
                font-size: 10px;
                line-height: 20px;
                font-weight: 200;
                span {
                  padding: 0px 3px;
                }
              }
              h3 {
                font-size: 13px;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
              }
            }
            &:nth-child(1) {
              display: none;
            }
          }
        }
      }
    }
  }

  &.active {
    transition: all 0.4s;
    position: absolute;
    top: 0;
    left: 0;
  }
}
</style>
